html,
body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  user-select: none;
  background: #f0f0f0;
}

#root > div {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}



.c {
  position: absolute;
  max-width: 1500px;
  max-height: 500px;
  box-shadow: 4px 8px 28px rgba(39,44,49,.1), 1px 3px 8px rgba(39,44,49,.075);
  width: 70ch;
  /* width: 50ch; */
  /* width: '100%'; */
  height: 50ch;
  cursor: pointer;
  background-color: '#0E2539';
  background: '#0E2539';
  color: '#0E2539';
  /* will-change: transform, opacity; */
}

.front,
.back {
  background-size: cover;
  background: 'blue';
}

.back {
  background-image: url(https://source.unsplash.com/gM8igOIP5MA/1600x900)
}
.front {
/* .front { */
  /* background-image: url(https://source.unsplash.com/SH_IjrKwG8c/1600x900) */
  background-color: '#0E2539';
}
